
/* ---- modal content ---- */

#modal > #overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .3);
    z-index: 1000;
}

#modal > #modal-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* padding: 1rem 2rem; */
    width: 757.42px;
    height: 277.23px;
    z-index: 1000;
    font-weight: bold;
    border-radius: 1rem;
    text-align: center;
    
}
#modal #modal-title{
    /* border-radius: 2rem 2rem 0rem 0rem; 위쪽 두군대 round처리 */
    /* margin: -1rem -2rem 0.5rem -2rem;    grid-item의 padding을 bottom빼고 무시 */
    margin-top: 7px;
    text-align: center;
    font-size: 25.97px;
    padding: 17.3px;
    border-bottom: 0.1rem solid #ccc;
    width: 604.49px;
    display: inline-block;

}
#modal #modal-content{
    height: 130px;
    /* padding: 2rem 1rem; */
    font-size: 25.97px;
    text-align: center;
    line-height: 130px;
    overflow:auto;  /*scroll*/
}
    /*scroll*/
#modal-content::-webkit-scrollbar {
    width: 0.5rem;
    background-color: #fff;
}
#modal-content::-webkit-scrollbar-thumb {
    background-color: #8CB5E4;
    border-radius:2rem;
}
#modal-content::-webkit-scrollbar-track {
    background-color: #dadada;
    border-radius: 2rem;
}
#modal #button-div{
    border: 1px solid #f00;
    padding: 0rem -2rem -1rem -2rem;    

}
#modal button{
    /* margin: 0rem -2rem -1rem -2rem;     */
    width: 50%;
    height: 76.53px;
    /* padding: 0.2rem 2rem; */
    /* border-radius: 0.5rem; */
    font-weight: bold;
    font-size: 25.97px;

}
#modal #ok-button{
    float: right;
    border: 0rem solid #336CFB;
    border-top: 0.1rem solid #336CFB;

    border-radius: 0rem 0rem 1rem 0rem; /*위쪽 두군대 round처리*/

}

#modal #cancle-button{
    float: left;
    border: 0rem solid #73778D;
    border-top: 0.1rem solid #73778D;
    border-radius: 0rem 0rem 0rem 1rem; /*위쪽 두군대 round처리*/

}