#map{
    width: 100%;
    height: 100%;
    position: relative;    
}
/* ---- map content ---- */
#map .grid-container {
    grid-template-columns: 1243.7px 285.5px;
}

#map #map-top {
    margin: 38.4px;
}
#map #map-top #discription {
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 24.7px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    /* text-align: center; */
    /* color: #336cfb; */
    padding-bottom: 21.1px;
    border-bottom: 2.3px solid #ced5e7;

}
#map #map-top #discription .icon-div{
    width: 54.9px;
    height: 54.9px;
    flex-grow: 0;
    box-shadow: 2.7px 2.7px 2.7px 0 rgba(16, 30, 115, 0.08);
    background-color: #336cfb;
    padding: 3px 8px;
    margin-right: 15px;
    border-radius: 50%;
    font-size: 35px;
}

#map .grid-item .grid-title{
    font-family: NotoSans;
    font-size: 24.2px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #73778d;
    padding: 40px 0 24.6px 0;
    border-bottom: 2.3px solid #ced5e7;
    margin: 0px;
}
/*--------------로봇--------------*/
#map-container .target {
    position: absolute;
    z-index: 9;
    /* background-color: rgb(0, 0, 0); */
    height:1px;
    background-color: #124D8455;
}
#map-container .robot {
    width: 50px;
    height: 50px; 
    position: absolute;
    z-index: 10;
    /* top: 10%; */
    /* margin: -0.4rem 0rem 0rem -0.4rem; */
    /* border: 1px solid rgb(102, 15, 143); */

}
#map #map-container .robot-icon-img {
    width: 100%;
    /* height: 100%; */
    position: absolute;
    z-index: 11;
    /* margin: 0.2rem; */
}
#map-container{
    position: relative;
    width: 1200px;
    /* border: 1px solid rgb(102, 15, 143); */
    /* width: 40rem; */
    /* height: 30rem; */
    height: 90%;
}
#map .table-button {
    width: 128px;
    height: 86px;
    position: absolute;
    border-radius: 24.7px;
    box-shadow: 0 5.5px 5.5px 0 rgba(0, 0, 0, 0.1);
    z-index: 2;
}

#map .table-button-name{
    text-align: center;
    line-height: 86px;
    font-family: NotoSans;
    font-size: 40px;
    font-weight: bold;
    width: 100%;
    height: 100%;
    z-index: 2;

}
#map .table-button:hover {
    background-color: #B1B1B1;
}
.map-tile {
        /* width: 1rem;
    height: 1rem; */
    border: 1px solid rgb(180, 173, 224);
    position: absolute;
    z-index: 2;
}

.map-tile-node-id{
    position: absolute;
    z-index: 2;
    top: 0.2rem;
    left: 0.2rem;
        /* border: 1px solid rgb(102, 15, 143); */
    width: 100%;
    height: 100%;
    margin: -0.2rem 0rem 0rem -0.2rem;
}
#map .map-tile-node-id:hover {
    background-color: rgba(104, 116, 167, 0.397);
}

.map-tile-img {
    position: absolute;
    z-index: 1;
}

/* 가로 36rem */

#map .map-background-img {  
    width: 100%;
    /* height: 100%; */
    /* border: 1px solid rgb(102, 15, 143); */

}
/*---------로봇상태------------------*/
#map #item-status {
    /* height: 100%; */
    box-sizing: border-box;
    /* border: 1px solid rgb(110, 92, 226); */
    
}

#map #item-status .item-scroll{
    /* border: 1px solid rgba(20, 202, 60, 0.692); */
    /* height: calc(100%-10rem); */
    /* height: 6rem; */
    /* margin-bottom:  10rem; */
    /* top: 0rem; */
    /* display: block; */
}
#map .home-button {
    box-sizing: border-box;
    padding: 0.4rem 1.2rem;
    margin-bottom: 0.5rem;
}

#map .home-button:hover {
    background-color: rgba(104, 116, 167, 0.993);
}
#map #item-status .icon {
    float: right;
    margin-left: 0rem;
    padding-left: 0rem;
}

/*---------로봇서빙현황-------------------*/
#map #item-serving {
    position: relative;
}
#map #item-serving #robot-serving-item {
    width: 100%;
    height: 5rem;
    padding: 1rem 0.3rem;
    border-bottom: 0.1rem solid #ccc;
}
#map #item-serving .robot-icon-img {
    height: 3rem;
    line-height: 5rem;
    vertical-align:middle;
}
#map #item-serving #serving-info {
    width: 70%;
    font-weight: bold;
    font-size: 0.9rem;
}
#map #item-serving #arrive-robot {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;

    /*중앙정렬*/
    line-height: 1.5rem;
    text-align: center;
}
#map #item-serving #arrive-robot-string{
    margin-right: 0.3rem;
}
#map #item-serving #arrow-div {
    width: 1rem;
    height: 1rem;
    display: inline-block;
    margin: 0.5rem;
}
#map #item-serving #arrow-right-div{
    display: inline-block;
    animation: mymove infinite;
    animation-duration: 1s;
}
#map #item-serving #arrow-right-div p:last-child{
    position: absolute;
}
#map #item-serving #arrow-right{
	width: 0;
	height: 0;
	border-top: 0.4rem solid transparent;
	border-bottom: 0.4rem solid transparent;
	border-left: 0.4rem solid #336CFB;/* 화살표 */
    display: inline-block;
}

@keyframes mymove {
from {margin-left: 0px;}
to {margin-left: 0.3rem;}
}
/*모니터링*/
#map #item-robot {
    position: relative;
}
#map #item-robot #robot-info{
}

#map #item-robot #robot-info #info-text{
    padding: 50px;
}
/*-----------error-control-button---------------*/
#map  #error-control{
    text-align: center;
    position: absolute;
    bottom: 37px;
}
#map  #error-control *{
    display: inline-block;
    width: 217px;
    height: 72px;
    border-radius: 15px;
    border-style: none;
    margin-top: 23px;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    box-shadow: 0 5.3px 5.3px 0 rgba(0, 0, 0, 0.1);
    }
#map #error-control #return {
    border: solid 2.8px #558eff;
    display: inline-block;
}
/* ---- monitoring content ---- */
#map #robot-info #robot-basic-img-div{
    text-align: center;
    vertical-align:middle;
    width: 100%;
    padding: 2rem;
}
#map #robot-info .robot-basic-img{
    width: 80%;
    max-width: 7rem;
}
#map #robot-info #info-text{
    font-weight: bold;
}