#dashboard{
    width: 100%;
    height: 100%;
    position: relative;    
}
#dashboard > .grid-container {
    grid-template-rows: 456px 412px;
}
#dashboard .grid-item{
    padding-left: 58px;
}

#dashboard .grid-title{
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #73778d;
    height: 30px;
    border-left: 12px solid #558eff;
    border-radius: 0;
    margin: 25px 0 0 0;
    padding: 0 0 0 11px;
}
#dashboard .grid-item .sub-grid-container{
    display: grid;
    box-sizing: inherit;    /*부모 기준으로 크기 정함*/
    gap: 33px 41px;  /*1.5rem;*/
    margin-top: 19px;
}
#dashboard #item-store .sub-grid-container{
    grid-template-rows: 158.8px 147px;
    grid-template-columns: 354px 455.7px 560.5px;
}

#dashboard #item-robot .sub-grid-container{
    grid-template-columns: 516px 892px;
    height: 298.7px;
}
#dashboard .grid-item .sub-grid-item{
    border-radius: 6.7px;
    box-shadow: 0 5.3px 5.3px 0 rgba(0, 0, 0, 0.05);
    background-color: #f7f9ff;
    position: relative;
}  
#dashboard #item-store .sub-grid-container #type-order{
    grid-column: 2;
    grid-row: 1/3;
}
#dashboard #item-store .sub-grid-container #time-order{
    grid-column: 3;
    grid-row: 1/3;
}
#dashboard #item-store .sub-grid-item{
    padding: 16px 13px 0 16px;
}
#dashboard #item-store .sub-grid-title{
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #73778d;
    text-align: left;
    /* border: 1px blue solid; */
    float: left;
}
#dashboard #item-store .sub-grid-item .order-count{
    position: absolute;
    bottom: 15px;
    left: 70px;
    width: 150px;
    font-family: NotoSans;
    font-size: 66.7px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: 4px;
    text-align: right;
    color: #558eff;
}
#dashboard #item-store #today-order .order-count{
    bottom: 20px;

}
#dashboard #item-store .sub-grid-item .gun{
    position: absolute;
    bottom: 37.7px;
    left: 217px;
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 40px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 40px;
    text-align: center;
    color: #558eff;
}
#dashboard #item-store #today-order .gun{
    bottom: 42.7px;
}
#dashboard .sub-grid-item .period-div{
    font-family: Noto Sans;
    font-style: normal;
    font-size: 25px;
    line-height: 30px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #9B9FB6;
    float: right;
    
}
#dashboard .sub-grid-item p{
    padding-left: 8px;
    padding-right: 8px;
    font-family: Noto Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18.6299px;   
    line-height: 30px;
    height: 100%;
    color: #9B9FB6;
    /* border: 1px red solid; */
}
#dashboard .sub-grid-item .week{
    color: #558EFF;
}
 /* ----------배달 홀 */
#dashboard #item-store .doughnut-graph{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 90px;
    left: 124px;
    /* background-color: yellow; */
}
#dashboard #item-store .sub-grid-item .value{
    position: absolute;
    top: 161px;
}
#dashboard .sub-grid-item #delivery{
    left: 30px;
}
#dashboard .sub-grid-item #hall{
    left: 339px;
}
#dashboard .sub-grid-item .value .type{
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #9b9fb6;
}
#dashboard .sub-grid-item .value .count{
    /* height: 33px; */
    margin-top: 7px;
    margin-bottom: 7px;
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    background-color: #49d7e3;
    align-items: center;
    padding: 2px 7px;
}
#dashboard .sub-grid-item .value .percent{
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 18.6px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #49d7e3;
}
#dashboard .sub-grid-item #hall *{
    text-align: left;

}
#dashboard .sub-grid-item #hall .count{
    background-color: #558eff;
}

#dashboard .sub-grid-item #hall .percent{
    color: #558eff;
}

